

/** telkeywoed,
 * reversal
 * @method checkPhoneNumber(tel,keyWord,reversal)
 * @for check.js
 * @param{String,String,Boolean} 		tel	, keyWord , reversal
 * @return {Boolean} 			,telfalse,reversaltruefalse
 */
function checkPhoneNumber(tel, keyword = false, reversal = false) {
	let reg = /^0?1[3|4|5|6|7|8|9][0-9]\d{8}$/;
	if (!keyword) {
		return reg.test(tel);
	} else {
		if (tel.length <= 0) {
			uni.showToast({
				icon: "none",
				title: keyword + "",
				duration: 2000
			});
			return reversal;
		}
		if (!reg.test(tel)) {
			uni.showToast({
				icon: "none",
				title: keyword + "",
				duration: 2000
			})
			return reversal;
		}
		return !reversal;
	}
}

/** emailkeywoed,
 * reversal
 * @method checkEmail(email,keyWord,reversal)
 * @for check.js
 * @param{String,String,Boolean} 		email , keyWord , reversal
 * @return {Boolean} 			,telfalse,reversaltruefalse
 */
function checkEmail(email, keyword = false, reversal = false) {
	let reg = new RegExp(/^([a-zA-Z]|[0-9])(\w|-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/);
	if (!keyword) {
		return reg.test(email);
	} else {
		if (email.length <= 0) {
			uni.showToast({
				icon: "none",
				title: keyword + "",
				duration: 2000
			});
			return reversal;
		}
		if (!reg.test(email)) {
			uni.showToast({
				icon: "none",
				title: keyword + "",
				duration: 2000
			})
			return reversal;
		}
		return !reversal;
	}
}
