import Vue from 'vue';
import {
	host, imgUrl
} from '@/config/config.js'
// token
var expired = false;
let requestRecord = {

}
let httpConfig = {
	header: {
		'Content-Type': "application/json",
		'AC-TOKEN': uni.getStorageSync('userinfo').token ?? '',
	},
	method: 'POST',
	showLoading: true, // loading
	loadingText: '...',
	loadingTime: 800, // ms
	timer: null, // 
	loadingMask: false, // loading
	stopRepeat: false, // 
	timeout: 15000, // 
}
//
function request(url, params, other) {
	other = {
		...httpConfig,
		...other
	};
	other.header['AC-TOKEN'] = uni.getStorageSync('userinfo').token ?? ''
	return new Promise((resolve, reject) => {
		if (other.stopRepeat) {
			if (requestRecord.url === true) {
				reject();
				return;
			}
			requestRecord.url = true;
		}
		// loading
		// timerid
		// loading
		if (other.showLoading && !other.timer) {
			httpConfig.timer = setTimeout(() => {
				uni.showLoading({
					title: other.loadingText,
					mask: other.loadingMask
				})
				other.timer = null;
			}, other.loadingTime);
		}
		uni.request({
			url: host + url,
			data: params,
			header: other.header,
			method: other.method,
			timeout: other.timeout,
			complete: data => {
				// loading(loading)
				uni.hideLoading();
				// loading
				clearTimeout(httpConfig.timer);
				httpConfig.timer = null;
				if (data.statusCode == 200) {
					if (data.data.msg == '' || data.data.msg == '' && !expired) {
						uni.removeStorage({
							key: 'token'
						})
						uni.reLaunch({
							url: '/pages/login/login'
						});
						expired = true;
						resolve({
							data: {},
							msg: data.data.msg
						});
					} else {
						expired = false;
						resolve(data.data);
					}
				} else {
					console.log(host + url, data);
					resolve({
						data: {},
						code: -1,
						success: false,
						msg: ""
					});
				}
				if (other.stopRepeat && requestRecord.url === true) {
					requestRecord.url = false;
				}
			}
		});
	})
}

function getRequest(url, params, other) {
	return request(url, params, {
		...other,
		method: 'GET'
	})
}

function postRequest(url, params, other) {
	return request(url, params, {
		...other,
		method: 'post'
	})
}

function getHost() {
	return host;
}
function getImgUrl() {
	return imgUrl
}

function getFile(id) {
	return `${host}/sysFileInfo/preview?id=${id}`;
}
export default {
	init() {
		return {
			request,
			getRequest,
			postRequest,
			getHost,
			getImgUrl,
			getFile
		}
	}
}
